@import "variables";
@import "flex";
@import "main";



header {
  background: transparent;
  padding: 2rem 1rem;
  border-bottom: 1px solid $laravel-border-color;

  .row {
    margin: 0 auto;
  }


  .header-logo {
    font-size: 2rem;
    font-weight: 500;
    p{margin: 0}
  }

  .header-right {
    text-align: right;
    @include flexbox();
    @include align-items(center);
    @include justify-content(flex-end);
    ul.menu {
      list-style: none;
      margin: 0;
    }
  }
}


.welcome {
  margin-top: 10rem;

  &-header {
    font-size: 3.5rem;
    margin: 0;
  }
  &-paragraph {
    font-size: 1.2rem;
    width: 40rem;
  }

  &-search {

    .search-input{
      padding: 1rem 2rem;
      font-size: 1.5rem;
      width: 40rem;
    }

  }

  &-links {
    margin-top: .5rem;
  }

}





@import "flexboxgrid.min.scss";
